@import "colours";

 
.home {
    width: 100%;
    height: 100vh;
    background-color: $second;
    border-radius: 0 0 200px 200px;


   >  main{
        position: absolute;
        //text-transform: uppercase;
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        width: 100%;
        height: 100vh;
        padding: 2rem;
        justify-content: center;

        h1{
            font: 400 6rem $text1;
            color: $first;
        }
        p{
            font: 900 1rem $text1;
            text-transform: uppercase;
            color: $first;
        }
    }

    &::before{
        content: "";
        width: 100%;
        height: 100vh;
        position: absolute;
        background: no-repeat center/auto url("../assets/1.png");
        filter: blur(4px);
    border-radius: 0 0 200px 200px;

    }
}

.home2 {
    width: 100%;
    height: 100vh;
    background-color: $second;
    padding: 500px 0 ;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: -200px;


    img{
        width: 30%;
        animation: imgUp 0.7s linear infinite alternate;
    }

    > div{
        width: 50%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 5rem;

        h1{
            font: 100 1.5rem $text1;
            
        }
        p{
            font: 100 1rem cursive;
            color: $first;
            
            
        }

    }

}

@keyframes imgUp{
    from{
        transform: translateY(0);
    }
    to{
        transform: translateY(-10px);
    }
}

.home3{
    width: 100%;
    height: 100vh;
    background-color: $second;
    margin-top: -200px;

    main{
        width: 70%;
        height: 80%;
        padding: 2rem;
        border-radius: 0 200px 200px 0;
        background-color: $first;
        align-items: center;
        justify-content: center;
        display: flex;
        flex-direction: column;

        >h1{
            font : 700 3rem $text2;
            text-transform: uppercase;
            
            padding: 0.5rem;
            padding-bottom: 1rem;
            color: $dark;
        }
        p{
            font : 500 1rem cursive;    
            font-style: italic;
            padding: 0.5rem;
            color: $dark;
        }
    }

}

.home4{
    width: 100%;
    height: 100vh;
    background-color: $second;
    justify-content: flex-end;
    display: flex;
    
    > div{
        background-color: $third;
        border-radius: 200px 0 0 200px;
        width: 70%;
        height: 75vh;
        padding: 2rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;


        >h1{
            color: white;
            text-transform: uppercase;
            width: full;
            padding: 0.5rem;
        }

        >article{
            display: flex;
            margin: 2rem 0 ;
            flex-wrap: wrap;
            justify-content: center;

            div {
                animation: imgUp 0.7s linear infinite alternate;
                margin: 1rem;
                height: 10rem;
                width: 10rem;
                background-color: $first;
                border-radius: 50%;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                color: $dark;
                cursor: pointer;
                transition: all 0.5s;
                &:hover {
                  filter: invert(1);
                }
        
                svg {
                  font-size: 4rem;
                }

            }
        }

    }
    
}